body {
  text-align: center;
  font-family: Arial, sans-serif;
}

h1 {
  font-size: 24px;
}

.search-form {
  margin: 20px 0;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.photo-card {
  text-align: center;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  text-align: center;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 0 auto;
  padding: 10px 20px;
  display: none;
}

.load-more:hover {
  background-color: #0056b3;
}

